  --primary-color: rgb(19, 41, 19);
  --primary-color-darker: rgb(8, 12, 6);
  --container-color: #1d1b1b;
  --white-color: #ffffff;
  --light-gray-color: #c0c0c0;
  --light-green-color: rgb(96, 175, 96);
  --title-fonts: url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
  --text-fonts: url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap');
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background-color: black;
  background-position-x: center;
  background-position-y: center;
  background-position: static;
  background-size: 100%;
  font-family: 'Open sans', sans-serif ;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 800px;
  margin: 120px auto;
  background: var(--container-color);
  padding: 30px;
  border-radius: 10px;
  color: var(--white-color);
}

.lista-section {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  height: 1000px;
  background-position: static;
  background-size: cover;
}

.envio-tarefa * {
  display: block;
  width: 90%;
  margin-bottom: 10px;
  margin: auto;
}

.tarefa {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.btn-tarefa:focus {
  outline: 1px solid var(--primary-color);
}

.btn-tarefa {
  border: none;
  color: var(--white-color);
  font-size: 18px;
  font-weight: 700;
  height: 40px;
  cursor: pointer;
  margin-top: 20px;
  background: linear-gradient(90deg, rgb(32, 155, 32), rgb(15, 120, 15));
  box-shadow: 0 4px 15px rgb(20, 67, 26);
  transition: box-shadow 0.3 ease, transform 0.3 ease;
}

.btn-tarefa:hover {
   background: linear-gradient(90deg, rgb(41, 186, 41), rgb(17, 133, 17));
  box-shadow: 0 4px 15px rgb(16, 54, 21);
  transform: translateY(-1px);
}

.titulo-lista {
  text-align: center;
  margin-bottom: 70px;
  font-size: 50px;
  font-family: var(--title-fonts);
  font-weight: 700;
}

.div-lista {
  margin-bottom: 50px;
}

.lista-tarefas li {
  font-size: 30px;
  color: #ffffff;
  padding: 20px;
  align-items: baseline;
  max-width: 560px;
  font-family: var(--text-fonts);
}

.checked-button,
.delete-button {
  height: 40px;
  margin: 10px;
  margin-top: 20px;
  color: var(--white-color);
  border: none;
  width: 70px;
  cursor: pointer;
  font-size: 20px;
  font-family: var(--text-fonts);
  border-radius: 50px;
}

.checked-button {
  background: linear-gradient(90deg, rgb(32, 155, 32), rgb(15, 120, 15));
  box-shadow: 0 4px 15px rgb(20, 67, 26);
  transition: box-shadow 0.3 ease, transform 0.3 ease;
  margin-left: 40px;
}
.checked-button:hover{
   background: linear-gradient(90deg, rgb(41, 186, 41), rgb(17, 133, 17));
  box-shadow: 0 4px 15px rgb(16, 54, 21);
  transform: translateY(-1px);

}

.delete-button {
  background: linear-gradient(90deg,  rgb(151, 24, 24), rgb(91, 12, 12));
  box-shadow: 0px 4px 15px rgb(85, 28, 28);
}

.delete-button:hover {
  background: linear-gradient(90deg, rgb(173, 30, 30), rgb(120, 16, 16));
  box-shadow: 0 4px 15px rgb(74, 24, 24);
  transform: translateY(-1px);
}

.checado {
  color: rgba(17, 244, 47, 0.63) !important;
  text-decoration: line-through !important;
}

/* CSS Landing Page */

.menu {
  position: fixed;
  /*perde o status de bloco e fixa ele no topo*/
  top: 0;
  /*pra ele nao descer caso outro elemento seja mexido?*/
  left: 0;
  right: 0;
  width: 100%;
  align-items: center;
  z-index: 1;
  /*faz com que o menu nao seja ultrapassado por outros elementos*/
  height: 60px;
  border-bottom: 0.1rem solid var(--light-gray-color);
  justify-content: center;
  background-color: var(--white-color);
}

.menu-content {
  display: flex;
  /*solta parte do menu*/
  justify-content: space-between;
  /*coloca um espaco entre os conteudos*/
  padding-top: 0;
  padding-bottom: 0;
  height: 60px;
  align-items: center;
}

.menu ul {
  list-style: none;
  /*tira as bolinhas da ul*/
  display: flex;
  /*solta*/
}

.menu ul li a {
  display: block;
  /*bota o txt lado a lado*/
  padding: 2rem;
  color: var(--primary-color);
  font-size: 1.2rem;
  position: relative;
  text-decoration: none;
}

.menu h1 {
  font-size: 1.7rem;
  color: var(--primary-color);
  padding: 2rem;
}

.menu h1 a {
  color: inherit;
  /*faz com que o elemento herde a cor de seu "pai"*/
}

/*inicio*/
.menu ul li a::after {
  content: '';
  position: absolute;
  bottom: 30px;
  left: 50%;
  width: 0;
  height: 0.1rem;
  background: var(--primary-color);
  transition: all 300ms ease-in-out;
}

.menu ul li a:hover::after {
  width: 50%;
  left: 25%;
}

/*tudo daqui pra cima foi pra botar efeitinho no menu*/

#apresentacao-inicial {
  height: 800px;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-position: static;
  background-size: cover;

}

.txt-apresentacao {
  margin: 0px 40px 0px 40px;
  padding-top: 100px;
}


.txt-tasklist,
.txt-gastos {
  padding-top: 200px;
  padding-left: 60px;
  padding-left: 60px;
}

#tasklist-page,
#gastos-page {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  padding: 20px;
}

.txt-tasklist,
.txt-gastos {
  flex: 1;
}

.tasklist-img {
  flex: 0;
}

.txt-apresentacao h2 {
  color: var(--white-color);
  font-size: 80px;
  margin-left: 70px;
  font-family: var(--title-fonts);
  margin-bottom: 30px;
  line-height: normal;


}

#tasklist-page h2,
#gastos-page h2 {
  color: var(--light-green-color);
  font-size: 70px;
  padding-left: 70px;
  font-family: var(--title-fonts);
  padding-bottom: 20px;
  line-height: normal;

}

.txt-apresentacao,
.txt-tasklist p,
.txt-gastos p {
  padding-left: 60px;
  padding-right: 60px;
  font-size: 30px;
  letter-spacing: 0.2px;
  line-height: 50px;
  color: var(--white-color);
  font-family: var(--text-fonts);
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  margin-bottom: 60px;
}

.txt-tasklist aside img,
.txt-gastos aside img {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
}

#tasklist-page {
  height: 900px;
  background-color: black;
  color: var(--white-color);
}

#tasklist-page button, #gastos-page button{
  padding: 15px 20px;
  border: none;
  background: linear-gradient(90deg, rgb(3, 110, 3), rgb(1, 30, 1));
  color: var(--white-color);
  font-size: 40px;
  height: 80px;
  cursor: pointer;
  margin-top: 80px;
  border-radius: 50px;
  width: 200px;
  margin: 30px;
  margin-bottom: 90px;
  align-content: center;
  display: block;
  margin-left: 100px;
  box-shadow: 0 4px 15px rgb(20, 67, 26);
  transition: box-shadow 0.3 ease, transform 0.3 ease;
  
}

#tasklist-page button:hover, #gastos-page button:hover {
  background: linear-gradient(90deg, rgb(8, 127, 8), rgb(1, 40, 1));
  box-shadow: 0 10px 20px rgb(20, 67, 26, 0.5);
  transform: translateY(-1px);
}


#tasklist-page #gastos-page, a {
  text-decoration: none;
  color: var(--white-color);
  font-family: var(--title-fonts);
}


#gastos-page {
  height: 1000px;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: static;
  background-size: cover;
}

.footer-page {
  background-color: var(--white-color);
  color: var(--primary-color);
  top: 0;
  /*pra ele nao descer caso outro elemento seja mexido?*/
  left: 0;
  right: 0;
  width: 100%;
  align-items: center;
  height: 100px;
  border-top: 0.1rem solid var(--light-gray-color);
  justify-content: center;
}

.footer-page ul {
  list-style: none;
}

.footer-page ul li a {
  color: var(--primary-color);
  font-size: 1.2rem;
  position: relative;
  text-decoration: none;
}
.footer-page ul li a img{
  max-height: 50px;
  margin: 0 20px 0 20px;
}

.footer-links {
  display:flex;
  align-items: center;
  justify-content: center;
