tulo-lista {\r\n  text-align: center;\r\n  margin-bottom: 70px;\r\n  font-size: 50px;\r\n  font-family: var(--title-fonts);\r\n  font-weight: 700;\r\n}\r\n\r\n.div-lista {\r\n  margin-bottom: 50px;\r\n}\r\n\r\n.lista-tarefas li {\r\n  font-size: 30px;\r\n  color: #ffffff;\r\n  padding: 20px;\r\n  align-items: baseline;\r\n  max-width: 560px;\r\n  font-family: var(--text-fonts);\r\n}\r\n\r\n.checked-button,\r\n.delete-button {\r\n  height: 40px;\r\n  margin: 10px;\r\n  margin-top: 20px;\r\n  color: var(--white-color);\r\n  border: none;\r\n  width: 70px;\r\n  cursor: pointer;\r\n  font-size: 20px;\r\n  font-family: var(--text-fonts);\r\n  border-radius: 50px;\r\n}\r\n\r\n.checked-button {\r\n  background: linear-gradient(90deg, rgb(32, 155, 32), rgb(15, 120, 15));\r\n  box-shadow: 0 4px 15px rgb(20, 67, 26);\r\n  transition: box-shadow 0.3 ease, transform 0.3 ease;\r\n  margin-left: 40px;\r\n}\r\n.checked-button:hover{\r\n   background: linear-gradient(90deg, rgb(41, 186, 41), rgb(17, 133, 17));\r\n  box-shadow: 0 4px 15px rgb(16, 54, 21);\r\n  transform: translateY(-1px);\r\n\r\n}\r\n\r\n.delete-button {\r\n  background: linear-gradient(90deg,  rgb(151, 24, 24), rgb(91, 12, 12));\r\n  box-shadow: 0px 4px 15px rgb(85, 28, 28);\r\n}\r\n\r\n.delete-button:hover {\r\n  background: linear-gradient(90deg, rgb(173, 30, 30), rgb(120, 16, 16));\r\n  box-shadow: 0 4px 15px rgb(74, 24, 24);\r\n  transform: translateY(-1px);\r\n}\r\n\r\n.checado {\r\n  color: rgba(17, 244, 47, 0.63) !important;\r\n  text-decoration: line-through !important;\r\n}\r\n\r\n/* CSS Landing Page */\r\n\r\n.menu {\r\n  position: fixed;\r\n  /*perde o status de bloco e fixa ele no topo*/\r\n  top: 0;\r\n  /*pra ele nao descer caso outro elemento seja mexido?*/\r\n  left: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  align-items: center;\r\n  z-index: 1;\r\n  /*faz com que o menu nao seja ultrapassado por outros elementos*/\r\n  height: 60px;\r\n  border-bottom: 0.1rem solid var(--light-gray-color);\r\n  justify-content: center;\r\n  background-color: var(--white-color);\r\n}\r\n\r\n.menu-content {\r\n  display: flex;\r\n  /*solta parte do menu*/\r\n  justify-content: space-between;\r\n  /*coloca um espaco entre os conteudos*/\r\n  padding-top: 0;\r\n  padding-bottom: 0;\r\n  height: 60px;\r\n  align-items: center;\r\n}\r\n\r\n.menu ul {\r\n  list-style: none;\r\n  /*tira as bolinhas da ul*/\r\n  display: flex;\r\n  /*solta*/\r\n}\r\n\r\n.menu ul li a {\r\n  display: block;\r\n  /*bota o txt lado a lado*/\r\n  padding: 2rem;\r\n  color: var(--primary-color);\r\n  font-size: 1.2rem;\r\n  position: relative;\r\n  text-decoration: none;\r\n}\r\n\r\n.menu h1 {\r\n  font-size: 1.7rem;\r\n  color: var(--primary-color);\r\n  padding: 2rem;\r\n}\r\n\r\n.menu h1 a {\r\n  color: inherit;\r\n  /*faz com que o elemento herde a cor de seu \"pai\"*/\r\n}\r\n\r\n/*inicio*/\r\n.menu ul li a::after {\r\n  content: '';\r\n  position: absolute;\r\n  bottom: 30px;\r\n  left: 50%;\r\n  width: 0;\r\n  height: 0.1rem;\r\n  background: var(--primary-color);\r\n  transition: all 300ms ease-in-out;\r\n}\r\n\r\n.menu ul li a:hover::after {\r\n  width: 50%;\r\n  left: 25%;\r\n}\r\n\r\n/*tudo daqui pra cima foi pra botar efeitinho no menu*/\r\n\r\n#apresentacao-inicial {\r\n  height: 800px;\r\n  background: url(../img/bg1.png);\r\n  background-position: static;\r\n  background-size: cover;\r\n\r\n}\r\n\r\n.txt-apresentacao {\r\n  margin: 0px 40px 0px 40px;\r\n  padding-top: 100px;\r\n}\r\n\r\n\r\n.txt-tasklist,\r\n.txt-gastos {\r\n  padding-top: 200px;\r\n  padding-left: 60px;\r\n  padding-left: 60px;\r\n}\r\n\r\n#tasklist-page,\r\n#gastos-page {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  gap: 20px;\r\n  padding: 20px;\r\n}\r\n\r\n.txt-tasklist,\r\n.txt-gastos {\r\n  flex: 1;\r\n}\r\n\r\n.tasklist-img {\r\n  flex: 0;\r\n}\r\n\r\n.txt-apresentacao h2 {\r\n  color: var(--white-color);\r\n  font-size: 80px;\r\n  margin-left: 70px;\r\n  font-family: var(--title-fonts);\r\n  margin-bottom: 30px;\r\n  line-height: normal;\r\n\r\n\r\n}\r\n\r\n#tasklist-page h2,\r\n#gastos-page h2 {\r\n  color: var(--light-green-color);\r\n  font-size: 70px;\r\n  padding-left: 70px;\r\n  font-family: var(--title-fonts);\r\n  padding-bottom: 20px;\r\n  line-height: normal;\r\n\r\n}\r\n\r\n.txt-apresentacao,\r\n.txt-tasklist p,\r\n.txt-gastos p {\r\n  padding-left: 60px;\r\n  padding-right: 60px;\r\n  font-size: 30px;\r\n  letter-spacing: 0.2px;\r\n  line-height: 50px;\r\n  color: var(--white-color);\r\n  font-family: var(--text-fonts);\r\n  display: flex;\r\n  flex-flow: column wrap;\r\n  justify-content: center;\r\n  margin-bottom: 60px;\r\n}\r\n\r\n.txt-tasklist aside img,\r\n.txt-gastos aside img {\r\n  display: flex;\r\n  flex-flow: column wrap;\r\n  justify-content: center;\r\n}\r\n\r\n#tasklist-page {\r\n  height: 900px;\r\n  background-color: black;\r\n  color: var(--white-color);\r\n}\r\n\r\n#tasklist-page button, #gastos-page button{\r\n  padding: 15px 20px;\r\n  border: none;\r\n  background: linear-gradient(90deg, rgb(3, 110, 3), rgb(1, 30, 1));\r\n  color: var(--white-color);\r\n  font-size: 40px;\r\n  height: 80px;\r\n  cursor: pointer;\r\n  margin-top: 80px;\r\n  border-radius: 50px;\r\n  width: 200px;\r\n  margin: 30px;\r\n  margin-bottom: 90px;\r\n  align-content: center;\r\n  display: block;\r\n  margin-left: 100px;\r\n  box-shadow: 0 4px 15px rgb(20, 67, 26);\r\n  transition: box-shadow 0.3 ease, transform 0.3 ease;\r\n  \r\n}\r\n\r\n#tasklist-page button:hover, #gastos-page button:hover {\r\n  background: linear-gradient(90deg, rgb(8, 127, 8), rgb(1, 40, 1));\r\n  box-shadow: 0 10px 20px rgb(20, 67, 26, 0.5);\r\n  transform: translateY(-1px);\r\n}\r\n\r\n\r\n#tasklist-page #gastos-page, a {\r\n  text-decoration: none;\r\n  color: var(--white-color);\r\n  font-family: var(--title-fonts);\r\n}\r\n\r\n\r\n#gastos-page {\r\n  height: 1000px;\r\n  background: url(../img/bg2.png);\r\n  background-position: static;\r\n  background-size: cover;\r\n}\r\n\r\n.footer-page {\r\n  background-color: var(--white-color);\r\n  color: var(--primary-color);\r\n  top: 0;\r\n  /*pra ele nao descer caso outro elemento seja mexido?*/\r\n  left: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  align-items: center;\r\n  height: 100px;\r\n  border-top: 0.1rem solid var(--light-gray-color);\r\n  justify-content: center;\r\n}\r\n\r\n.footer-page ul {\r\n  list-style: none;\r\n}\r\n\r\n.footer-page ul li a {\r\n  color: var(--primary-color);\r\n  font-size: 1.2rem;\r\n  position: relative;\r\n  text-decoration: none;\r\n}\r\n.footer-page ul li a img{\r\n  max-height: 50px;\r\n  margin: 0 20px 0 20px;\r\n}\r\n\r\n.footer-links {\r\n  display:flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}"],"sourceRoot":""}]);
